body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --index: calc(1vw + 1vh);
    --text: #e7e7e0;
}

@font-face {
    font-family: raleway_f;
    src: url(/parallax_web/static/media/raleway-regular.957a49ea.woff2);
}

@font-face {
    font-family: raleway_f;
    src: url(/parallax_web/static/media/raleway-black.294f721c.woff2);
    font-weight: 900;
}

.App {
    background-color: #010101;
    font-family: raleway_f, sans-serif;
    line-height: 1.55;
}

.main-header {
    position: relative;
}

.main-header::after {
    content: '';
    position: absolute;
    z-index: 100;
    width: 100%;
    height: calc(calc(1vw + 1vh) * 10);
    height: calc(var(--index) * 10);
    background-image: url(/parallax_web/static/media/ground.4e09537a.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    bottom: calc(calc(1vw + 1vh) * -4.5);
    bottom: calc(var(--index) * -4.5);
}

.layers {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
    position: relative;
}

.layer {
    height: 100%;
    width: 100%;
    position: absolute;
    background-size: cover;
    background-position: center;
    will-change: transform;
    z-index: 2;
}

.layers_base {
    -webkit-transform: translate3d(0, calc(var(--scrollTop) / 1.6), 0);
            transform: translate3d(0, calc(var(--scrollTop) / 1.6), 0);
    z-index: 0;
}

.layers_middle {
    -webkit-transform: translate3d(0, calc(var(--scrollTop) / 2.5), 0);
            transform: translate3d(0, calc(var(--scrollTop) / 2.5), 0);
}

.layers_front {
    -webkit-transform: translate3d(0, calc(var(--scrollTop) / 5.7), 0);
            transform: translate3d(0, calc(var(--scrollTop) / 5.7), 0);
}

.layers_header {
    z-index: 1;
    -webkit-transform: translate3d(0, calc(var(--scrollTop) / 2), 0);
            transform: translate3d(0, calc(var(--scrollTop) / 2), 0);
    will-change: transform;
    text-transform: uppercase;
    font-weight: 900;
    color: #e7e7e0;
    color: var(--text);
    text-shadow: 0 0 15px #9d822b;
}

.layers_title {
    font-size: calc(calc(1vw + 1vh) * 2.5);
    font-size: calc(var(--index) * 2.5);
    letter-spacing: calc(calc(1vw + 1vh) / 2.25);
    letter-spacing: calc(var(--index) / 2.25);
}

.layers_caption {
    font-size: calc(calc(1vw + 1vh) / 1.12);
    font-size: calc(var(--index) / 1.12);
    letter-spacing: calc(calc(1vw + 1vh) / 3.5);
    letter-spacing: calc(var(--index) / 3.5);
    margin-top: calc(calc(1vw + 1vh) * -0.8);
    margin-top: calc(var(--index) * -0.8);
}

.main-article {
    min-height: 100vh;
}

.layer.layers_base {
    background-image: url(/parallax_web/static/media/layer-base.e17d3f76.png);
}

.layer.layers_middle {
    background-image: url(/parallax_web/static/media/layer-middle.859e14f4.png);
}

.layer.layers_front {
    background-image: url(/parallax_web/static/media/layer-front.76998f86.png);
}

.main-article {
    --article-transform: translate3d(0, calc(var(--scrollTop) / -7.5), 0);
    background-image: url(/parallax_web/static/media/dungeon.00fdadb2.jpg);
    background-size: cover;
    background-position: center;
    color: #e7e7e0;
    color: var(--text);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    text-align: center;
}

.main-article_header {
    text-transform: uppercase;
    font-size: calc(calc(1vw + 1vh) * 1.8);
    font-size: calc(var(--index) * 1.8);
    letter-spacing: calc(calc(1vw + 1vh) / 7.6);
    letter-spacing: calc(var(--index) / 7.6);
    -webkit-transform: var(--article-transform);
            transform: var(--article-transform);
    will-change: transform;
}

.main-article_p {
    max-width: calc(calc(1vw + 1vh) * 30);
    max-width: calc(var(--index) * 30);
    font-size: calc(calc(1vw + 1vh) * 0.76);
    font-size: calc(var(--index) * 0.76);
    margin-top: calc(calc(1vw + 1vh) / 1.3);
    margin-top: calc(var(--index) / 1.3);
    -webkit-transform: var(--article-transform);
            transform: var(--article-transform);
    will-change: transform;
}

.copy {
    position: absolute;
    bottom: calc(calc(1vw + 1vh)* 2.5);
    bottom: calc(var(--index)* 2.5);
    opacity: 0.48;
    font-size: calc(calc(1vw + 1vh) * 0.74);
    font-size: calc(var(--index) * 0.74);
    letter-spacing: calc(calc(1vw + 1vh) / 36);
    letter-spacing: calc(var(--index) / 36);
}

